#cover {
    height: 100vh;
    display: flex;
    align-items: center;
    justify-content: center;
}

#cover-logo {
    max-width: 20vw;
}
#cover-title {
    font-size: calc(5rem + 5vw);
    margin-left: calc(-.4rem - .4vw);
}
#cover-description {
    font-size: 20px;
}
#cover-email {
    font-size: 18px;
}