p {
    color: var(--color-09);
    text-align: justify;
}
h1, h2, h3, h4, h5, h6, strong {
    color: var(--color);
}
code {
    color: var(--primary);
}

::selection {
    background-color: var(--primary);
    color: var(--on-primary);
}

*::-webkit-scrollbar {
    width: 10px;
}
*::-webkit-scrollbar-track {
    background: var(--surface);
}
*::-webkit-scrollbar-thumb {
    background-color: var(--on-surface);
    width: 10px;
    border: transparent;
}

:root {
    --transition: all .5s ease;
    --border-radius: 10px;
}

.negative-margin {
    margin: -10px;
}